:root {
  --purple: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --dark-purple: hsl(270, 9%, 17%);
  --gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --grayish-purple: hsl(273, 4%, 51%);
}

.section-two {
  display: flex;
  background-color: var(--dark-purple);
  color: var(--white);
  font-family: "Karla";
  font-size: 0.67rem;
  font-weight: 100;
  line-height: 1rem;
  height: 25rem;
  padding-left: 8rem;
  padding-right: 8rem;
  padding-top: 3.6rem;
}
.section-two a {
  text-decoration: none;
}

.containern {
  margin: 0;
  width: 23.5rem;
  padding-bottom: 1.5rem;
}

.happy {
  width: 23.5rem;
  padding-top: 1.4rem;
}